import React, {useEffect} from 'react';

function WhyBest() {
  // scrolled to top when redirected from a page
  useEffect(() => {
    window.scrollTo(0, 0);
  }, []);

  return (
    <div>
      <div id="details" className="whybest">
        <div className="container">
          <div className="row">
            <div className="col-lg-6 col-xl-7">
              <div className="image-container">
                <img
                  className="img-fluid"
                  src="assets/images/whybest1.png"
                  alt="alternative"
                />
                <img
                  className="img-fluid"
                  src="assets/images/whybest2.png"
                  alt="alternative"
                />
              </div>
            </div>

            <div className="col-lg-6 col-xl-5">
              <div className="text-container">
                <div className="section-title"></div>
                <h2>Extra Crazy Features</h2>
                <p>
                  Computer vision based wall and floor changer. Change paints
                  and wallpapers behinde objects too.
                </p>
                {/* <a className="btn-solid-reg" href="#contact">Get quote</a> */}
                <br />
                <br />
                <br />

                <h4>
                  {" "}
                  <i class="fas fa-shopping-basket"></i> Reliable and Secure
                  Platform
                </h4>
                <h4>
                  {" "}
                  <i class="fas fa-map-marked-alt"></i> Everything is perfectly
                  organized
                </h4>
                <h4>
                  {" "}
                  <i class="fas fa-shopping-basket"></i> Simple Line Icon
                </h4>
                <h4>
                  {" "}
                  <i class="fas fa-map-marked-alt"></i> Step on the new leve
                </h4>
              </div>
            </div>
          </div>
        </div>
      </div>
    </div>
  );
}

export default WhyBest;
